<template>
    <ion-page>
      <ion-header>
        <ion-toolbar>
          <ion-title>Semua Ujian SNBT</ion-title>
          <ion-back-button slot="start" default-href="/" />
        </ion-toolbar>
      </ion-header>
      <ion-content>
        <ion-grid>
          <ion-row>
            <!-- Tampilkan semua data ujian SNBT -->
            <ion-col
                size="6"
                v-for="ujian in daftarUjiansnbt.filter(ujian => ujian.aktif === true)"
                :key="ujian.id"
                @click="navigateToDetailUjiansnbt(ujian.id)"
                style="cursor: pointer;"
              >
              <div style="border: 1px solid #ddd; padding: 15px; border-radius: 8px; background-color: #8e44ad; margin: 5px; display: flex; flex-direction: column; align-items: center; justify-content: space-between; height: 150px;">
                <p style="text-align: center; font-size: 16px; font-weight: bold; color: #fff; flex: 1; display: flex; align-items: center; justify-content: center;">{{ ujian.judulUjian }}</p>
                <button style="background-color: #4CAF50; color: white; border: none; padding: 10px 20px; font-size: 14px; border-radius: 5px; cursor: pointer;">
                  Mulai
                </button>
              </div>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>
    </ion-page>
  </template>
  
  <script setup>
  import {
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonPage,
  IonContent,
  IonGrid,
  IonHeader,
  IonCol,
  IonRow,
} from "@ionic/vue";
  import { ref, onMounted } from 'vue';
  import { useRouter } from 'vue-router';
  import { getFirestore, collection, getDocs } from 'firebase/firestore';
  import { firebaseApp } from '../firebase.js'; // Sesuaikan dengan path firebase.js Anda
  
  const router = useRouter();
  const daftarUjiansnbt = ref([]);
  
  // Ambil data ujian SNBT dari Firestore
  const fetchDaftarUjiansnbt = async () => {
    try {
      const db = getFirestore(firebaseApp);
      const querySnapshot = await getDocs(collection(db, "jadwalsnbt"));
      daftarUjiansnbt.value = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
    } catch (error) {
      console.error("Error fetching daftar ujian SNBT:", error);
    }
  };
  
  // Navigasi ke detail ujian
  const navigateToDetailUjiansnbt = (jadwalId) => {
    router.push({ name: "ujiansnbt", params: { jadwalId } });
  };
  
  // Ambil data saat komponen dimuat
  onMounted(() => {
    fetchDaftarUjiansnbt();
  });
  </script>